import dayjs from 'dayjs';

const months = '___________'.split(
  '_'
);

const locale = {
  name: 'ar',
  weekdays: '______'.split('_'),
  weekdaysShort: '______'.split('_'),
  weekdaysMin: '______'.split('_'),
  months,
  monthsShort: months,
  weekStart: 6,
  meridiem: (hour, _, isUpperCase) => {
    if (hour >= 12) {
      return isUpperCase ? '' : '';
    }
    return isUpperCase ? '' : '';
  },
  relativeTime: {
    future: ' %s',
    past: ' %s',
    specialNumeration: (l, n) => {
      const y = l.substring(0, 1);
      const str = {
        s: [' ', '', '', ''],
        m: [' ', '', '', ''],
        h: [' ', '', '', ''],
        d: [' ', '', '', ''],
        w: [' ', '', '', ''],
        M: [' ', '', '', ''],
        y: [' ', '', '', ''],
      };

      const x = Number(String(n).substr(-2, 2));

      if (n < 2) {
        return `${str[y][0]} `;
      }

      if (x < 2) {
        return `%d ${str[y][1]}`;
      }

      if (x > 2) {
        if (n < 11) {
          return `%d ${str[y][3]}`;
        }
        return `%d ${str[y][1]}`;
      }

      if (n === 2) {
        return str[y][2];
      }

      return `%d  ${str[y][3]}`;
    },
  },
  ordinal: (n) => n,
  formats: {
    LT: 'HH:mm',
    LTS: 'HH:mm:ss',
    L: 'D/M/YYYY',
    LL: 'D MMMM YYYY',
    LLL: 'D MMMM YYYY HH:mm',
    LLLL: 'dddd D MMMM YYYY HH:mm',
  },
  calendar: {
    sameDay: 'h:mm A',
    nextDay: '[]',
    nextWeek: 'dddd',
    lastDay: '[]',
    lastWeek: 'dddd []',
    sameElse: 'D MMMM',
  }
};

dayjs.locale(locale, null, true);

export default locale;
